"use client";

import { useEffect, useRef, useState } from "react";

const projectsData = [
  {
    title: "E-Commerce Automation",
    description: "Automated inventory management and customer service for a leading retail brand, reducing response time by 80%.",
    industry: "Retail",
    image: "",
    results: ["80% faster responses", "24/7 availability", "$2M saved annually"],
    slug: "ecommerce-automation"
  },
  {
    title: "Healthcare AI Assistant",
    description: "Intelligent patient scheduling and diagnosis support system for medical practices.",
    industry: "Healthcare",
    image: "",
    results: ["50% more patients", "Zero missed appointments", "95% satisfaction"],
    slug: "healthcare-ai"
  },
  {
    title: "Financial Data Analytics",
    description: "Real-time market analysis and predictive modeling for investment firms.",
    industry: "Finance",
    image: "",
    results: ["99.9% accuracy", "Real-time insights", "3x ROI"],
    slug: "financial-analytics"
  },
  {
    title: "Manufacturing Optimization",
    description: "Smart factory automation with predictive maintenance and quality control.",
    industry: "Manufacturing",
    image: "",
    results: ["40% efficiency gain", "Zero downtime", "Quality improved"],
    slug: "manufacturing-optimization"
  },
  {
    title: "Legal Document AI",
    description: "Automated contract analysis and legal research assistant for law firms.",
    industry: "Legal",
    image: "",
    results: ["10x faster research", "100% compliance", "Cost reduced 60%"],
    slug: "legal-document-ai"
  },
  {
    title: "EdTech Learning Platform",
    description: "Personalized learning paths and automated grading system for educational institutions.",
    industry: "Education",
    image: "",
    results: ["Personalized learning", "90% engagement", "Teacher time saved"],
    slug: "edtech-platform"
  }
];

export default function Projects() {
  const [isVisible, setIsVisible] = useState(false);
  const [hoveredIndex, setHoveredIndex] = useState<number | null>(null);
  const sectionRef = useRef<HTMLElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const sceneRef = useRef<any>(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.1 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, []);

  useEffect(() => {
    if (!canvasRef.current) return;

    const THREE = window.THREE;
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
    const renderer = new THREE.WebGLRenderer({
      canvas: canvasRef.current,
      alpha: true,
      antialias: true
    });

    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
    camera.position.z = 50;

    // Create particle constellation system
    const particleCount = 4000;
    const positions = new Float32Array(particleCount * 3);
    const colors = new Float32Array(particleCount * 3);
    const velocities = new Float32Array(particleCount * 3);
    const originalPositions = new Float32Array(particleCount * 3);

    const goldColor = new THREE.Color(0xD4AF37);
    const whiteColor = new THREE.Color(0xffffff);
    const brightGold = new THREE.Color(0xFFD700);

    // Create constellation clusters for each project
    const clustersPerProject = Math.floor(particleCount / projectsData.length);

    for (let i = 0; i < particleCount; i++) {
      const i3 = i * 3;
      const clusterIndex = Math.floor(i / clustersPerProject);
      const angle = (clusterIndex / projectsData.length) * Math.PI * 2;
      const radius = 25 + Math.random() * 15;

      // Position particles in constellation clusters
      const clusterX = Math.cos(angle) * radius;
      const clusterY = Math.sin(angle) * radius * 0.6;
      const spread = 8;

      positions[i3] = clusterX + (Math.random() - 0.5) * spread;
      positions[i3 + 1] = clusterY + (Math.random() - 0.5) * spread;
      positions[i3 + 2] = (Math.random() - 0.5) * 20;

      originalPositions[i3] = positions[i3];
      originalPositions[i3 + 1] = positions[i3 + 1];
      originalPositions[i3 + 2] = positions[i3 + 2];

      velocities[i3] = (Math.random() - 0.5) * 0.02;
      velocities[i3 + 1] = (Math.random() - 0.5) * 0.02;
      velocities[i3 + 2] = (Math.random() - 0.5) * 0.02;

      // Color variation - more gold in cluster centers
      const distFromCenter = Math.sqrt(
        Math.pow(positions[i3] - clusterX, 2) +
        Math.pow(positions[i3 + 1] - clusterY, 2)
      );
      const color = distFromCenter < spread * 0.3 ? goldColor :
        Math.random() > 0.6 ? brightGold : whiteColor;

      colors[i3] = color.r;
      colors[i3 + 1] = color.g;
      colors[i3 + 2] = color.b;
    }

    const geometry = new THREE.BufferGeometry();
    geometry.setAttribute('position', new THREE.BufferAttribute(positions, 3));
    geometry.setAttribute('color', new THREE.BufferAttribute(colors, 3));

    const material = new THREE.PointsMaterial({
      size: 2.5,
      vertexColors: true,
      transparent: true,
      opacity: 0.9,
      blending: THREE.AdditiveBlending,
      sizeAttenuation: true
    });

    const particles = new THREE.Points(geometry, material);
    scene.add(particles);

    // Create dynamic connection lines
    const maxConnections = 1500;
    const linePositions = new Float32Array(maxConnections * 6);
    const lineColors = new Float32Array(maxConnections * 6);

    const lineGeometry = new THREE.BufferGeometry();
    lineGeometry.setAttribute('position', new THREE.BufferAttribute(linePositions, 3));
    lineGeometry.setAttribute('color', new THREE.BufferAttribute(lineColors, 3));

    const lineMaterial = new THREE.LineBasicMaterial({
      vertexColors: true,
      transparent: true,
      opacity: 0.2,
      blending: THREE.AdditiveBlending
    });

    const lines = new THREE.LineSegments(lineGeometry, lineMaterial);
    scene.add(lines);

    // Mouse interaction
    let mouseX = 0, mouseY = 0;
    let targetMouseX = 0, targetMouseY = 0;

    const handleMouseMove = (e: MouseEvent) => {
      targetMouseX = (e.clientX / window.innerWidth) * 100 - 50;
      targetMouseY = -(e.clientY / window.innerHeight) * 60 + 30;
    };

    window.addEventListener('mousemove', handleMouseMove);

    // Animation loop
    let time = 0;
    const animate = () => {
      time += 0.01;

      // Smooth mouse following
      mouseX += (targetMouseX - mouseX) * 0.05;
      mouseY += (targetMouseY - mouseY) * 0.05;

      const posArray = particles.geometry.attributes.position.array;
      let connectionCount = 0;

      // Update particles with gravity and physics
      for (let i = 0; i < particleCount; i++) {
        const i3 = i * 3;

        // Gentle drift
        posArray[i3] += velocities[i3];
        posArray[i3 + 1] += velocities[i3 + 1];
        posArray[i3 + 2] += velocities[i3 + 2];

        // Mouse gravitational pull
        const dx = mouseX - posArray[i3];
        const dy = mouseY - posArray[i3 + 1];
        const dist = Math.sqrt(dx * dx + dy * dy);
        const force = 40 / (dist + 15);

        velocities[i3] += dx * force * 0.0008;
        velocities[i3 + 1] += dy * force * 0.0008;

        // Return to original position (spring effect)
        const returnForce = 0.002;
        velocities[i3] += (originalPositions[i3] - posArray[i3]) * returnForce;
        velocities[i3 + 1] += (originalPositions[i3 + 1] - posArray[i3 + 1]) * returnForce;
        velocities[i3 + 2] += (originalPositions[i3 + 2] - posArray[i3 + 2]) * returnForce;

        // Damping
        velocities[i3] *= 0.97;
        velocities[i3 + 1] *= 0.97;
        velocities[i3 + 2] *= 0.97;

        // Wave motion
        posArray[i3 + 2] += Math.sin(time + i * 0.01) * 0.05;
      }

      // Update connections dynamically
      connectionCount = 0;
      const connectionDistance = 12;
      const sampleRate = 3; // Check every 3rd particle for performance

      for (let i = 0; i < particleCount && connectionCount < maxConnections; i += sampleRate) {
        const i3 = i * 3;

        for (let j = i + sampleRate; j < Math.min(i + 100, particleCount) && connectionCount < maxConnections; j += sampleRate) {
          const j3 = j * 3;

          const dx = posArray[i3] - posArray[j3];
          const dy = posArray[i3 + 1] - posArray[j3 + 1];
          const dz = posArray[i3 + 2] - posArray[j3 + 2];
          const dist = Math.sqrt(dx * dx + dy * dy + dz * dz);

          if (dist < connectionDistance) {
            const c = connectionCount * 6;
            linePositions[c] = posArray[i3];
            linePositions[c + 1] = posArray[i3 + 1];
            linePositions[c + 2] = posArray[i3 + 2];
            linePositions[c + 3] = posArray[j3];
            linePositions[c + 4] = posArray[j3 + 1];
            linePositions[c + 5] = posArray[j3 + 2];

            const opacity = 1 - (dist / connectionDistance);
            const colorArray = particles.geometry.attributes.color.array;

            lineColors[c] = colorArray[i3];
            lineColors[c + 1] = colorArray[i3 + 1];
            lineColors[c + 2] = colorArray[i3 + 2];
            lineColors[c + 3] = colorArray[j3];
            lineColors[c + 4] = colorArray[j3 + 1];
            lineColors[c + 5] = colorArray[j3 + 2];

            connectionCount++;
          }
        }
      }

      particles.geometry.attributes.position.needsUpdate = true;
      lines.geometry.attributes.position.needsUpdate = true;
      lines.geometry.attributes.color.needsUpdate = true;
      lines.geometry.setDrawRange(0, connectionCount * 2);

      // Subtle rotation
      particles.rotation.y = Math.sin(time * 0.1) * 0.1;
      lines.rotation.y = particles.rotation.y;

      renderer.render(scene, camera);
      sceneRef.current = requestAnimationFrame(animate);
    };

    animate();

    // Resize handler
    const handleResize = () => {
      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(window.innerWidth, window.innerHeight);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('resize', handleResize);
      if (sceneRef.current) {
        cancelAnimationFrame(sceneRef.current);
      }
      renderer.dispose();
      geometry.dispose();
      material.dispose();
      lineGeometry.dispose();
      lineMaterial.dispose();
    };
  }, []);

  return (
    <section
      id="projects"
      ref={sectionRef}
      className="section-padding bg-[#111111] relative overflow-hidden"
    >
      {/* Three.js Particle Constellation Canvas */}
      <canvas
        ref={canvasRef}
        className="absolute inset-0 w-full h-full pointer-events-none opacity-50"
        style={{ mixBlendMode: 'screen' }}
      />

      {/* Decorative Background */}
      <div className="absolute inset-0 pointer-events-none">
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[800px] h-[800px] bg-[#D4AF37] rounded-full opacity-[0.02] blur-3xl" />
        <div className="absolute top-1/4 right-1/4 w-96 h-96 bg-[#FFD700] rounded-full opacity-[0.03] blur-[100px]" />
        <div className="absolute bottom-1/4 left-1/4 w-96 h-96 bg-[#D4AF37] rounded-full opacity-[0.03] blur-[100px]" />
      </div>

      <div className="max-w-7xl mx-auto relative z-10">
        {/* Section Header */}
        <div
          className={`text-center mb-16 transition-all duration-700 ${isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
            }`}
        >
          <span className="inline-block text-[#D4AF37] text-sm font-semibold tracking-widest uppercase mb-4">
            Our Portfolio
          </span>
          <h2 className="text-3xl sm:text-4xl lg:text-5xl font-bold font-[var(--font-cormorant)] mb-6">
            <span className="text-[#f5f5f5]">Delivered </span>
            <span className="text-gradient-gold">Projects</span>
          </h2>
          <p className="text-[#a3a3a3] text-lg max-w-2xl mx-auto">
            Explore our track record of successful AI automation implementations
            across diverse industries.
          </p>
        </div>

        {/* Projects Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {projectsData.map((project, index) => (
            <div
              key={project.title}
              onMouseEnter={() => setHoveredIndex(index)}
              onMouseLeave={() => setHoveredIndex(null)}
              className={`group relative rounded-3xl glass-card border-[rgba(255,255,255,0.08)] overflow-hidden card-hover transition-all duration-700 ${isVisible
                ? "opacity-100 translate-y-0"
                : "opacity-0 translate-y-12"
                }`}
              style={{ transitionDelay: `${index * 100}ms` }}
            >
              {/* Project Image/Icon Area */}
              <div className="relative h-48 glass flex items-center justify-center overflow-hidden">
                <span className="text-6xl transform group-hover:scale-125 transition-all duration-500 group-hover:rotate-12">
                  {project.image}
                </span>
                <div className="absolute inset-0 bg-gradient-to-t from-[#0a0a0a] via-[rgba(10,10,10,0.5)] to-transparent opacity-80" />

                {/* Industry Badge */}
                <span className="absolute top-4 left-4 px-3 py-1.5 text-xs font-medium glass text-[#D4AF37] rounded-full border border-[rgba(212,175,55,0.3)] backdrop-blur-xl">
                  {project.industry}
                </span>

                {/* Constellation indicator */}
                <div className="absolute top-4 right-4 w-2 h-2 bg-[#D4AF37] rounded-full animate-pulse shadow-lg shadow-[#D4AF37]" />
              </div>

              {/* Content */}
              <div className="p-6 relative z-10">
                <h3 className="text-xl font-semibold text-[#f5f5f5] mb-3 font-[var(--font-cormorant)] group-hover:text-[#D4AF37] transition-colors">
                  {project.title}
                </h3>
                <p className="text-[#a3a3a3] text-sm mb-4 leading-relaxed">
                  {project.description}
                </p>

                {/* Results */}
                <div className="flex flex-wrap gap-2 mb-6">
                  {project.results.map((result) => (
                    <span
                      key={result}
                      className="px-2 py-1 text-xs bg-[rgba(255,255,255,0.03)] text-[#737373] rounded-md border border-[rgba(255,255,255,0.05)] group-hover:border-[rgba(212,175,55,0.3)] group-hover:text-[#D4AF37] transition-all duration-300"
                    >
                      {result}
                    </span>
                  ))}
                </div>

                {/* View Case Study Link */}
                <a
                  href={`#case-study-${project.slug}`}
                  className="inline-flex items-center gap-2 text-[#D4AF37] text-sm font-medium group/link hover:text-[#FFD700] transition-colors"
                >
                  View Case Study
                  <svg
                    className="w-4 h-4 group-hover/link:translate-x-1 transition-transform"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M17 8l4 4m0 0l-4 4m4-4H3"
                    />
                  </svg>
                </a>
              </div>

              {/* Bottom Border Accent */}
              <div className="absolute bottom-0 left-1/2 -translate-x-1/2 w-3/4 h-1 bg-gradient-to-r from-transparent via-[#D4AF37] to-transparent opacity-0 group-hover:opacity-100 transition-all duration-500" />

              {/* Glow effect */}
              <div className="absolute inset-0 rounded-3xl opacity-0 group-hover:opacity-100 transition-opacity duration-500 pointer-events-none shadow-[inset_0_0_60px_rgba(212,175,55,0.1)]" />

              {/* Particle burst on hover */}
              {hoveredIndex === index && (
                <div className="absolute inset-0 pointer-events-none">
                  <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full h-full bg-[#D4AF37] rounded-full opacity-10 blur-3xl animate-pulse" />
                </div>
              )}
            </div>
          ))}
        </div>

        {/* View All Projects CTA */}
        <div
          className={`text-center mt-12 transition-all duration-700 delay-500 ${isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
            }`}
        >
          <a href="#contact" className="btn-secondary inline-flex items-center gap-2">
            Discuss Your Project
            <svg
              className="w-5 h-5"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z"
              />
            </svg>
          </a>
        </div>
      </div>
    </section>
  );
}